<button @click="again()"  :disabled="seconds>0" id="get-password">Отправить код повторно</button>
</div>
</template>
<script>
import store from '../store/index'
import axios from 'axios'
  export default {
    data: () => ({
    start: '',
	interval:'',
	minutes:'',
	seconds:'',
	end: '',
	// time: '0:30',
	myInput: ''
    }),
	methods: {
		again(){
			let tel = store.getters.getNumberOk
             console.log(tel )
   var data = 'msisdn=' + tel
			axios.post(
  'http://api.sabil.webinfra.ru/sabil',
  `{"path": "https://api.sabeel.credo.ru:5543/api/auth", "data": ${JSON.stringify(data)}, "headers": {"Authorization": "Basic bW9iaWxlYXBwOlZvd29vOHpvb0o=", "Content-Type": "application/x-www-form-urlencoded"}}`,
  {
      headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
      }
  }
)
.then(function () {
 
  
})
.catch(function (error) {
  console.log(error);
});
		},
	updateValue(value) {
      console.log(value)
      },
	nextStep1() {
		this.start = new Date().getTime()
		this.end = new Date()
		this.end.setTime(this.start+30000)
		console.log(this.end, 'end')
		this.timerCount(this.start,this.end)
