<script>
import Vue from "vue";
import vSelect from "vue-select";
import axios from 'axios'
import store from '../store/index'
Vue.component("v-select", vSelect);
import "vue-select/dist/vue-select.css";
  export default {
    name: 'HelloWorld',
    data: () => ({
      ayatData:'',
