  async created(){
  // let newDate = (new Date().getTime()/1000).toFixed()
  let that = this



  let data2 = `surah=${store.getters.getAyat.surah}`
await   axios.post(
  'http://api.sabil.webinfra.ru/sabil',
   `{"path": "https://api.sabeel.credo.ru:5543/api/quran", "data": ${JSON.stringify(data2)}, "headers": {"Authorization": "Basic bW9iaWxlYXBwOlZvd29vOHpvb0o=", "Content-Type": "application/x-www-form-urlencoded"}, "cookies": {"PHPSESSID": "null"}}`,
  {
      headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
      }
  }
)
.then(function (response) {
    // cookieJar.myCookies = response.headers['Content-Type']; 
    // console.log(cookieJar.myCookies)
that.ayatData=JSON.parse(JSON.stringify(response.data.quran))

// that.namazData.sort(function(a,b){
//   return a-b
// })
console.log(JSON.stringify(response.data))

})
.catch(function (error) {
  console.log(error)
  return false

});
that.ayatData=that.ayatData.filter(x => {
  return x.stih == store.getters.getAyat.stih
})[1]

}
  }
